<template>
  <div class="page-container">
    <div v-if="loading">Memuat artikel...</div>
    <div v-if="error" class="error">{{ error }}</div>
    <article v-if="article">
      <h1 class="article-title">{{ article.title }}</h1>
      <p class="article-meta">
        Oleh <strong>{{ article.author.name }}</strong> dalam kategori <strong>{{ article.category.name }}</strong>
        <br>
        Dipublikasikan pada {{ formattedDate }}
      </p>
      <div class="article-content">
        <p v-for="(paragraph, index) in articleParagraphs" :key="index">{{ paragraph }}</p>
      </div>
      <router-link to="/articles" class="back-link">‚Üê Kembali ke Semua Artikel</router-link>
    </article>
  </div>
</template>

<script setup>
import { ref, onMounted, computed } from 'vue';
import { useRoute } from 'vue-router';
import ApiService from '@/services/ApiService';
import { formatDate } from '@/utils/formatDate';

const route = useRoute();
const article = ref(null);
const loading = ref(true);
const error = ref(null);

const formattedDate = computed(() => article.value ? formatDate(article.value.date_posted) : '');
const articleParagraphs = computed(() => article.value ? article.value.content.split('\n') : []);

onMounted(async () => {
  const articleId = route.params.id;
  try {
    const response = await ApiService.getArticle(articleId);
    article.value = response.data;
  } catch (err) {
    error.value = "Artikel tidak ditemukan atau terjadi kesalahan.";
    console.error(err);
  } finally {
    loading.value = false;
  }
});
</script>

<style scoped>
.page-container {
  max-width: 800px;
  margin: 40px auto;
  padding: 0 20px;
}
.article-title {
  font-size: 2.5em;
  margin-bottom: 10px;
}
.article-meta {
  color: #666;
  font-style: italic;
  margin-bottom: 30px;
  border-bottom: 1px solid #eee;
  padding-bottom: 20px;
}
.article-content {
  line-height: 1.8;
  font-size: 1.1em;
}
.back-link {
  display: inline-block;
  margin-top: 40px;
  text-decoration: none;
  font-weight: bold;
  color: #ff69b4;
}
</style>